* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: silver;
    height: 100%;
}

#potato,
.container,
body,
html {
    height: 100%;
    display: flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;

}
.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 0px;
    padding-top: 70px;
    width: 90%;
}

.movies .movie {
    width: 48%;
    background-color: white;
    margin-bottom: 70px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    font-weight: 300;
    padding: 20px;
    border-radius: 5px;
    color : #adaeb9;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(50, 50, 93, 0.025)


}

.movies img {   
    position: relative;
    top: -50px;
    max-width: 110px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(50, 50, 93, 0.025)

}

.movie .movie__title,
.movie .movie__year {
    margin: 0;                                              
    font-weight: 300;
}

.movie .movie__title {
    margin-bottom: 5px;
    font-size: 14px;
    color : #2c2c2c;

}

.movie .movie__genres {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    margin: 5px ;
}


.movie__genres li{
    margin-right: 10px;
    font-size: 12px;
}